.card {
    width: 63mm;
    height: 88mm;
    border-radius: 5.5mm;
    -webkit-box-shadow: 0px 0px 10px 5px rgba(0,0,0,0.45); 
    box-shadow: 0px 0px 10px 5px rgba(0,0,0,0.45);
    /* font-family: 'Jaldi', sans-serif; */
    font-family: 'Poppins', sans-serif;
    padding: 1rem;
    line-height: 1.4rem;
    font-size: 1rem;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    transition: all .5s ease-in-out;
    cursor: pointer;
    user-select: none;
}

.card.flip {

}

.card:hover {
    -webkit-box-shadow: 0px 0px 20px 5px rgba(0,0,0,0.45); 
    box-shadow: 0px 0px 20px 5px rgba(0,0,0,0.45);
    transform: scale(1.01 , 1.01);
}

.card .card-bonus {
    width: 100%;
    background-color: black;
    color: white;
    border-radius: 5.5mm;
}

.card .card-text {
    margin-top: .5rem;
    font-size: 1.2rem;
    line-height: 1.65rem;
    font-weight: 600;
}

.card .card-bonus .card-bonus-text {
    text-align: center;
    margin: .5rem 1rem;
    font-size: .75rem;
}